import React from 'react'
import { Container, Row, Col} from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import './index.css'
import Profile from './profile'
import TeamMembersData from './TeamMembersData'


class TeamMembers extends React.PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      backdrop: false,
    }
  }

  componentDidMount(){
    if(window.location.hash === '#team'){
      setTimeout(function(){
        document.getElementById('buttonPressKit').scrollIntoView();
      },400)
    }
  }
  toggleBackdrop = backdrop => {
    this.setState({ backdrop })
  }

  render() {
    return (

  <div className="container p-3  pt-0 pb-0" id="teamMembers">
    <div className="pt-20 pb-40 row">
    <Container>
            <Col>
                <h1 className="display-3 headline" style={{ color: '#ff6427', fontWeight: 'bold' }} >
                <FormattedMessage id="TeamMembers.title" />
                </h1>
            </Col>
          <Row  className="team-profile-row">
            <Col>
              <Row>
                {TeamMembersData.map(({ name, title, imageSrc, intro, linkedIn }) => (
                  <React.Fragment>
                    <Col key={name} md={3} sm={6} className="px-3 py-3">
                      <Profile
                        name={name}
                        title={title}
                        src={imageSrc}
                        intro={intro}
                        linkedIn={linkedIn}
                        toggleBackdrop={this.toggleBackdrop}
                      />
                    </Col>
                    </React.Fragment>
                        ))}
              </Row>
            </Col> 
            </Row>
        </Container>
    <div className={`backdrop + ${this.state.backdrop ? ' show' : ''}`} />
    </div>
  </div>
)
}
}


export default TeamMembers



