import React from 'react'
import { Container, Row, Col, Button, Card } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import './index.css'


const LatestActivity = () => (
    <div className="latestActivity-section">
      <div class="background-container">
        <div class="custom-shape custom-shape-1"></div>
      </div>
      <Container>
        <Row className="show-grid pt-40 pb-40 ">
            <Col  md={12}>
                <h1 className="display-3 headline" style={{ color: '#ff6427', fontWeight: 'bold' }}>
                <FormattedMessage id="LatestActivity.title" />
                </h1>
            </Col>
            <Col  md={6}>
              <Card className="LatestActivity-card">
                <Row style={{padding : '20px 20px 20px 30px'}}>
                <Col  md={4} className="mb-20 pl-6" >
                    <iframe src="https://www.instagram.com/p/Bp7FdjEgx65/embed/" allowtransparency="true" scrolling="no" frameborder="0"> </iframe>
                </Col>


                <Col  md={12} className="pl-6">
                <div className="text-orange">
                <a href="https://www.instagram.com/arestechio/" target="_blank">
                 <img src="/images/instagram.png" className="social-img"  alt="instagram" />
                </a>
                <FormattedMessage id="view.more.on.our.instagram" />
              </div>
              </Col>
                </Row>
              </Card>
            </Col>
            <Col  md={6}>
             <Row style={{marginTop: '20px'}} className="pl-1">
               <Col md={6} style={{paddingRight: '0'}}>
               <div className="mb-20 text-orange">
                  <a href="https://t.me/ares_tech" target="_blank">
                    <img src="/images/telegram.png" className="social-img"  alt="telegram" />
                  </a>
                <FormattedMessage id="telegram"/>
              </div>
              <div className="mb-20 text-orange">
                <a href="https://github.com/ares-tech" target="_blank">
                  <img src="/images/github.png" className="social-img"  alt="github" />
                </a>
                <FormattedMessage id="github" />
              </div>
              <div className="mb-20 text-orange">
                <a href="https://www.facebook.com/arestech.io" target="_blank">
                  <img src="/images/fb.png" className="social-img"  alt="facebook" />
                </a>
                <FormattedMessage id="facebook" />
              </div>
              </Col>
              <Col md={6} style={{paddingRight: '0'}}>
              <div className="mb-20 text-orange">
                <a href="https://twitter.com/arestech_io" target="_blank">
                 <img src="/images/twitter.png" className="social-img"  alt="twitter" />
                </a>
                <FormattedMessage id="twitter" />
              </div>
              <div className="mb-20 text-orange">
                <a href="https://www.youtube.com/channel/UCV_O9CKVKr4-KJVC-v8FcVw" target="_blank">
                  <img src="/images/youtube.png" className="social-img"  alt="youtube" />
                </a>
                <FormattedMessage id="youtube" />
              </div>
              <div className="mb-20 text-orange">
                <a href="https://medium.com/ares-tech" target="_blank">
                  <img src="/images/medium.png" className="social-img"  alt="medium" />
                </a>
                <FormattedMessage id="medium" />
              </div>
              </Col>
              <Col md={12}>
              <div className="pt-211">
            <a
              href="https://drive.google.com/drive/folders/14-JhpPGauii_8lcPyOUNzekS-EQSMEsP?usp=sharing"
              rel="noopener noreferrer"
              target="_blank"
              style={{ color: '#ff6427' }}
            >
              <Button className="main__button_press_kit" id="buttonPressKit">
                <i className="fa fa-newspaper-o" aria-hidden="true"></i>
                <span style={{ paddingLeft: '10px' }}>
                  <FormattedMessage id="contact.get.press.kit" />
                </span>
              </Button>
            </a>
          </div>
              </Col>
              </Row>
            </Col>
        </Row>
    </Container>
    </div>
)

export default LatestActivity



